import Vue from 'vue'
import Router from 'vue-router'
import store from "../store/index.js"

// 
import NavBar from '@/components/navBar'

//
import Home from '@/components/home/home'
//
import Events from '@/components/events/events'
import ApplyDetail from '@/components/events/applyDetail'
import ReviewDetail from '@/components/events/reviewDetail'
import Apply from '@/components/events/apply'
//
import Word from '@/components/mine/word'
import Mine from '@/components/mine/mine'
import Message from '@/components/mine/message'
import Renew from '@/components/mine/renew'
import Dues from '@/components/mine/dues'
import Standard from '@/components/mine/standard'
import User from '@/components/mine/user'
import Perfect from '@/components/mine/perfect'
import Perfect1 from '@/components/mine/perfect1'
import Order from '@/components/mine/order'
import Join from '@/components/mine/join'
import Event from '@/components/mine/event'
import Need from '@/components/mine/need'
import Collect from '@/components/mine/collect'
import About from '@/components/mine/about'
//
import Alliance from '@/components/alliance/alliance'
//
import Login from '@/components/login/login'
// 
import Pay from '@/components/pay/pay'
import PayConfirm from '@/components/pay/payConfirm'



// 
import BuyHouse from '@/components/home/buyHouse' //
import Announcement from '@/components/home/announcement' //
import AnnouncementInfo from '@/components/home/announcementInfo' //
import News from '@/components/home/news' //
import NewsInfo from '@/components/home/newsInfo' //
import JoinAlliance from '@/components/joinAlliance/joinAlliance' //
import MemberBenefits from '@/components/memberBenefits/memberBenefits' //
import JoinExecutiveDirector from '@/components/memberBenefits/joinExecutiveDirector' //
// 
import AllianceOne from '@/components/alliance/allianceOne'
import AllianceTwo from '@/components/alliance/allianceTwo'
//
import MemberNeeds from '@/components/memberNeeds/memberNeeds' //
import FindProject from '@/components/memberNeeds/findProject' //
import FindFund from '@/components/memberNeeds/findFund' //
import FindJob from '@/components/memberNeeds/findJob' //


Vue.use(Router)

const router =  new Router({
  // base: "/",
  mode: "history",
  scrollBehavior: ()=>({y:0}),//
  routes: [

    {path: '*',name:'navBar',redirect: "/home",component: NavBar,
        children:[
          {path: '/home',component: Home},
          {path: '/events',component: Events,meta:{keepAlive: true}},
          {path: '/alliance',component: Alliance},
          {path: '/mine',component: Mine}
        ]
    },
    //
    {path: '/applyDetail',component: ApplyDetail},//
    {path: '/reviewDetail',component: ReviewDetail},//
    {path: '/apply',component: Apply},//
    //
    {path: '/word',component: Word},//
    {path: '/message',component: Message},//
    {path: '/user',component: User},//
    {path: '/perfect',component: Perfect},//
    {path: '/perfect1',component: Perfect1},//
    {path: '/renew',component: Renew},//
    {path: '/dues',component: Dues},//
    {path: '/standard',component: Standard},//
    {path: '/order',component: Order},//
    {path: '/join',component: Join},//
    {path: '/event',component: Event},//
    {path: '/need',component: Need},//
    {path: '/collect',component: Collect},//
    {path: '/about',component: About},//
    // 
    {path: '/buyHouse',component: BuyHouse}, //
    {path: '/announcement',component: Announcement}, //
    {path: '/announcementInfo',component: AnnouncementInfo}, //
    {path: '/news',component: News}, //
    {path: '/newsInfo',component: NewsInfo}, //
    {path: '/joinAlliance',component: JoinAlliance}, //
    {path: '/memberBenefits',component: MemberBenefits}, //
    {path: '/joinExecutiveDirector',component: JoinExecutiveDirector}, //
    // 
    {path: '/allianceOne',component: AllianceOne},
    {path: '/allianceTwo',component: AllianceTwo},
    // 
    {path: '/memberNeeds',component: MemberNeeds}, //
    {path: '/findProject',component: FindProject}, //
    {path: '/findFund',component: FindFund}, //
    {path: '/findJob',component: FindJob}, //
    // 
    {path: '/login',component: Login},
    // 
    {path: '/pay',component: Pay},
    {path: '/payConfirm',component: PayConfirm},

  ],
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      if (from.meta.keepAlive) {
        from.meta.savedPosition = Number(localStorage.scrollTop);
      }
      return { x: 0, y: to.meta.savedPosition || 0}
    }
  }
})

export default router;



