<template>
  <div class="reviewDetail" ref="box">
    <div class="navTop">
      <img src="../../../static/images/common/backWhite.png" class="backWhite" @click="navBack()">
      <img src="../../../static/images/common/collect.png" class="collectIcon" @click="collectBtn()" v-if="collectId == 0">
      <img src="../../../static/images/common/collect1.png" class="collectIcon" @click="cancelCollectBtn()" v-else>
    </div>
    <div class="infoWrap">
      <h3 class="infoTitle">{{activityDetail.title}}</h3>
      <h6 class="infoDate">{{activityDetail.create_time}}</h6>
      <div class="infoContent" v-html="activityDetail.remark"></div>
    </div>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  export default {
    data () {
      return {
        urlId: "",
        move: "",
        newsObj: {},
        collectId: 0, // 1  2 
        activityDetail: "",//
        logo: "../../../static/images/mine/logo.png"
      }
    },
    beforeRouteEnter (to, from, next) {
      var u = navigator.userAgent;
      var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
      // XXX: iOSHTML5 History
      if (isiOS && to.path !== location.pathname) {
        // location.replace
        location.assign(to.fullPath)
      } else {
        next()
      }
    },
    mounted() {
      if(getUrlId("move")){
        $(".navTop").hide();
        $(".infoWrap").css({"padding": "0rem .2rem .3rem .2rem"})
      }
      this.urlId = this.$route.query.id || getUrlId("id");
      this.init();
    },
    updated(){
      this.fenxiangFun();
    },
    methods:{
      // 
      navBack(){
        if(this.$route.query.url){
          this.$router.push({path:"/events",query:{url:"reviewDetail"}});
        }else{
          this.$router.go(-1);
        }
      },
      init(){
        this.$http({
          url: this.changeData() + "/activity/selectActivityDetail",
          method: "post",
          params: {
            type :  2,//12
            id : this.urlId
          }
        }).then(function(res){
          console.log(res);
          if(res.data.code=="101"){
            this.activityDetail = res.data.activityDetail;
            this.collectId = this.activityDetail.is_collect;
          }else{
            Toast(res.data.message)
          }
        }.bind(this)).catch(function(err){
          console.log("",err)
        })
      },
      // 
      collectBtn(){
        if(!localStorage.token){
          this.$router.push({path: '/login'});
        }else{
          this.$http({
            url: this.changeData() + '/member/auth/collect',
            method: 'post',
            params: {
              type: "2", // 0 1
              id: this.urlId //id
            }
          }).then(res => {
            console.log(res);
            this.collectId = 1;
            if(res.data.code == 101){
              Toast('');
            }else{
              Toast(res.data.message)
            }
          }).catch(err => {
            console.log(err)
          });
        }
      },
      // 
      cancelCollectBtn(){
        if(!localStorage.token){
          this.$router.push({path: '/login'});
        }else{
          this.$http({
            url: this.changeData() + '/member/auth/cancelCollect',
            method: 'post',
            params: {
              collectType: "2", // 0 1
              id: this.urlId //id
            }
          }).then(res => {
            console.log(res);
            if(res.data.code == 101){
              this.collectId = 0;
              Toast('');
            }else{
              Toast(res.data.message);
            }
          }).catch(err => {
            console.log(err)
          });

        }
      },
      fenxiangFun(){//
        var that = this;
        this.$http({
          url:this.changeData() + '/member/weChatShare',
          method: "post",
          params: {
            url : window.location.href.split('#')[0]
          }
        }).then(function(res){
          // console.log(res)
          wx.config({
            //debug: true, // ,apialertpclogpc
            appId: res.data.appId, // 
            timestamp: res.data.timestamp, // 
            nonceStr: res.data.nonceStr, // 
            signature: res.data.signature,// 
            jsApiList: ["onMenuShareAppMessage","onMenuShareTimeline"] // JS
          });

          wx.ready(function(){
            // configreadyconfigconfigreadyready
            //
            wx.onMenuShareAppMessage({
              title: that.activityDetail.title, // 
              desc: '', // 
              link: window.location.href.split('#')[0], // JS
              imgUrl: "https://up-static.oss-cn-beijing.aliyuncs.com/Config/11544678901_.pic_hd.jpg", // 
              type: '', // ,musicvideolinklink
              dataUrl: '', // typemusicvideo
              success: function () {
                // 
              }
            });

            //
            wx.onMenuShareTimeline({
              title: that.activityDetail.title, // 
              link: window.location.href.split('#')[0], // JS
              imgUrl: "https://up-static.oss-cn-beijing.aliyuncs.com/Config/11544678901_.pic_hd.jpg", // 
              success: function () {
                // 
              }
            });
          });
          wx.error(function(res){
            // configerrorconfigdebugresSPA
          });
        }.bind(this)).catch(function(err){
          console.log("",err)
        });
      },
    },
  }
</script>

<style scoped>
  .infoContent >>> img{
    max-width: 6.9rem;
  }
</style>
<style lang="scss" type="text/scss" scoped>
  @import "../../../static/css/commonNav.css";
  .reviewDetail{
    width: 7.5rem;
    height: 100vh;
    background: #fff;
  }
  .infoWrap{
    background: #fff;
    padding: 1.4rem .2rem .3rem .2rem;
  .infoTitle{
    color: #333;
    font-size: .36rem;
    text-align: justify;
    line-height: .5rem;
    /*text-align: center;*/
  }
  .infoDate{
    font-size: .28rem;
    color: #898989;
    margin-top: .2rem;
    margin-bottom: .3rem;
  }
  .infoContent{
    p{
      color: #666;
      font-size: .28rem;
      text-indent: 2em;
      line-height: .4rem;
    }
  }
  }
</style>




