<template>
    <div class="user">
      <div class="nav"><span @click="back()"></span> <router-link to="/home"></router-link></div>
      <div class="content">
        <ol>
          <li v-for="item in imgList">
            <img :src="item.file.src" alt="">
          </li>
        </ol>
        <div class="img"><img :src="imgsrc" alt=""><input type="file" @change="uploadFun($event)"></div>
        <ul>
          <li><i></i><span>{{iphone}}</span></li>
          <li><i></i><input type="text" v-model="userName" placeholder=""></li>
          <li @click="actionsheet"><i></i><b></b><input type="text" placeholder="" v-model="sexName">
            <mt-actionsheet
              :actions="sex"
              v-model="sexhide">
            </mt-actionsheet>
          </li>
          <li @click="openPicker"><i></i><b></b><input type="text" placeholder="" v-model="birthday">
            <mt-datetime-picker
              type="date"
              ref="picker"
              year-format="{value} "
              month-format="{value} "
              date-format="{value} "
              @confirm="handleConfirm"
              :startDate="startDate"
            >
            </mt-datetime-picker></li>
          <li><i></i><input type="text" v-model="company" placeholder=""></li>
          <li><i></i><input type="text" v-model="userduty" placeholder=""></li>
        </ul>
      </div>
      <div class="confirm" @click="confirmFun()"></div>
    </div>
</template>

<script>
import { Toast,Actionsheet  } from 'mint-ui';
import axios from 'axios';
import qs from 'qs';
export default {
    data () {
        return {
           sex:[
             {name: "",method:this.getman},
             {name: "",method:this.getwomen},
           ],
            iphone: "",
            sexhide: false,
            userName:"",//
            sexName: "",//
            userduty: "",//
            imgsrc:"../../../static/images/mine/wanshang.png",//
            birthday: "",//
            company: "",//
            startDate: new Date('1968-01-01'),
            //
            host: "",
            policy:"",
            signature: "",
            accessid: "",
            dir: "",
            expire: "",
            callback: "",
            fileImg: "",
            imgList: []
        }
    },
    computed:{

    },
    mounted() {
      this.iphone = localStorage.iphone.substr(0,3)+"****"+localStorage.iphone.substr(7);
    },
    methods:{
      back(){
        this.$router.push({path:"/mine"});
      },
      getman(){
        this.sexName = "";
      },
      getwomen(){
        this.sexName = "";
      },
      actionsheet(){
        this.sexhide = true;
      },
      openPicker() {
        this.$refs.picker.open()
      },
      handleConfirm (data) {
        this.birthday = data;   //getdata
        var d = new Date(data);
        this.birthday = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
      },
      ossData() {
        let url = this.changeData() + '/oss/policy'
        axios(url, {
          method: 'post',
          url: url
        }).then(ret => {
          console.log(ret);
          this.host = ret.data.host;
          this.policy = ret.data.policy;
          this.signature = ret.data.signature;
          this.accessid = ret.data.accessid;
          this.dir = ret.data.dir;
          this.expire = ret.data.expire;
          this.callback = ret.data.callback;
        }).catch(err => {
          console.log(err)
        });
      },
      uploadFun(e){
        console.log(e)
        let _this = this;

        //1. fileinput, filefile file
        var file = e.target.files[0];
        this.fileImg = file;
        // console.log(file);

        //2. img
        //FileReader
        var reader = new FileReader();

        //3. 
        //readAsDataURL: dataurl base64 img
        reader.readAsDataURL(file);

        //4.  
        reader.onload = function() {
          // console.log(reader.result);
          //
          _this.imgDataUrl = reader.result;
          _this.isUpImg = "1";
        }
      },
      random_string(len) {
        len = len || 32;
        var chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz123456789';
        var maxPos = chars.length;
        var pwd = '';
        for (var i = 0; i < len; i++) {
          pwd += chars.charAt(Math.floor(Math.random() * maxPos));
        }
        return pwd;
      },
      confirmFun(){
        let _this = this;
        var ossData = new FormData();
        ossData.append('OSSAccessKeyId', _this.accessid);
        ossData.append('policy', _this.policy);
        ossData.append('Signature', _this.signature);
        //random
        ossData.append('key', _this.dir + _this.random_string(32));
        ossData.append('success_action_status', 200); // 
        ossData.append("callback", _this.callback);
        ossData.append("file", _this.fileImg);
        this.$http.post(this.host, ossData, { headers: { "Content-Type": "multipart/form-data" } })
          .then(res => {
            console.log(res);
            _this.imgsrc = res.data.fileName;

            if(_this.imgsrc){
              _this.upData();
            }
          }).catch(err => {
          console.log(err)
        });
      },
      upData() {
        this.$http({
          url: this.changeData() + "/member/auth/perfectMemberInfo",
          method: "post",
          params: {
            name: this.userName,
            sex: this.sexName,
            position: this.userduty,
            birthday: this.birthday,
            company: this.company,
            iconPath: "/" + this.imgsrc
          }
        }).then(function(res){
          console.log(res);
          if(res.data.code=="101"){
            this.$router.push({path:"/home"})
          }else{
            Toast(res.data.message)
          }
        }.bind(this)).catch(function(err){
          console.log(err)
        })
      }
    },
}
</script>

<style lang="scss" type="text/scss" scoped>
  @import "../../../static/css/common.scss";
  .user{
    margin-top: 1rem;
    position: relative;
    .nav{
      a{
        position: absolute;
        top: 0rem;
        right: .2rem;
        font-size: .3rem;
        color: #fff;
      }
    }
    .title{
      background: #fff;
      height: .8rem;
      line-height: .8rem;
      font-size: 0;
      text-align: center;
      border-bottom: .01rem solid #eaeaea;
      span{
        display: inline-block;
        width: 50%;
        height: .8rem;
        font-size: .3rem;
        position: relative;
        &.cur{
          b{
            display: block;
          }
        }
        b{
          display: none;
          position: absolute;
          width: .84rem;
          height: .04rem;
          left: 50%;
          margin-left: -.42rem;
          bottom: 0;
          background: #3C915C;
        }
      }
    }
    .content{
      margin-top: 1rem;
      background: #fff;
      .img{
        padding-top: .3rem;
        width: 1.86rem;
        height: 1.86rem;
        /*text-align: center;*/

        position: relative;
        img{
          width: 1.86rem;
          height: 1.86rem;
        }
        input{
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
        }

      }
      ul{
        padding: 0 .3rem;
        li{
          height: .9rem;
          line-height: .9rem;
          font-size: .3rem;
          border-bottom: .01rem solid #EAEAEA;
          i{
            display: inline-block;
            width: 1.8rem;
          }
          span{
            float: right;
          }
          b{
            width: 0.24rem;
            height: 0.15rem;
            margin-top: .4rem;
            float: right;
            background: url("../../../static/images/mine/xia.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
          }
          input{
            font-size: .28rem;
          }
        }
      }
    }
    .confirm{
      position: fixed;
      left: 0;
      bottom: 0;
      width: 7.5rem;
      height: 1rem;
      line-height: 1rem;
      text-align: center;
      color: #fff;
      background: #3C915C;
      font-size: .34rem;
    }
  }
</style>



