<template>
    <div class="collect">
      <div class="nav" @click="back()"><span></span> <b  @click.stop="logoutFun()"></b></div>
      <div class="content">
        <div class="msg">
          <div class="img"></div>
          <div class="name"></div>
          <div class="tel">010-57116916</div>
          <div class="txt">
            <p>200830009658128510928TOP20070%50%</p>

            <p>105---</p>

            <p></p>

            <p></p>

            <p></p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
import $ from '../../../static/js/jquery.min.js';
import axios from 'axios';
import qs from 'qs';
export default {
    data () {
        return {

        }
    },
    mounted() {
      if(getUrlId("move")){
        $(".nav").hide();
        $(".content").css({"margin": "0"})
      }
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      logoutFun(){
        localStorage.clear();
        this.$router.push({path:"/home"})
        Toast("")
      },
    },
}
</script>

<style lang="scss" type="text/scss" scoped>
  @import "../../../static/css/common.scss";
  .collect{
    width: 7.5rem;
    height: 100vh;
    background: #fff;
    position: relative;
    .nav{
      b{
        position: absolute;
        top: 0rem;
        right: .2rem;
      }
    }
    .content{
      margin-top: 1rem;
      width: 7.5rem;
      height: 21.41rem;
      background: url("../../../static/images/mine/about-bg.png") no-repeat;
      -webkit-background-size: 100% 100%;
      background-size: 100% 100%;
      .msg{
        padding: .7rem .3rem 1rem;
        .img{
          width: 1.48rem;
          height: 1.48rem;
          margin: 0 auto;
          background: url("../../../static/images/mine/logo.png") no-repeat;
          -webkit-background-size: 100% 100%;
          background-size: 100% 100%;
        }
        .name{
          text-align: center;
          font-size: .34rem;
          padding: .3rem 0 .2rem;
        }
        .tel{
          text-align: center;
          font-size: .3rem;
        }
        .txt{
          margin-top: .3rem;
          width: 6.3rem;
          background: #fff;
          padding: .3rem;
          p{
            font-size: .3rem;
            line-height: .5rem;
            text-indent: 2em;
          }
        }
      }
    }
  }
</style>



