<template>
<div class="findProject">
    <div class="navTop1">
        <img src="../../../static/images/common/backWhite.png" class="backWhite" @click="navBack()">
        <h2 class="navTopH2"></h2>
    </div>
    <div>
        <ul class="findProjectUl">
            <li>
                <p>*</p>
                <input type="" name="" placeholder="" v-model="linkmanName">
            </li>
            <li>
                <p>*</p>
                <input type="tel" name=""  placeholder="" onkeyup="this.value=this.value.replace(/\D/g,'')" maxlength="11" v-model="linkmanTel">
            </li>
            <li>
                <p>*</p>
                <input type="" name="" placeholder="" v-model="organization">
            </li>
            <li>
                <p>*</p>
                <input type="" name="" placeholder="" v-model="jobTitle">
            </li>
            <li class="lastLi">
                <p>*</p>
                <textarea maxlength="200" placeholder="..." v-model="needs"></textarea>
            </li>
        </ul>
        <div class="submitBtn" @click="submitBtn"></div>
        <!--  -->
        <div class="promptWrap" v-if="showPromptWrap">
            <div class="promptBox">
                <h5></h5>
                <ul>
                    <li class="li1" @click="goLeave()"></li>
                    <li class="li2" @click="showPromptWrap = false"></li>
                </ul>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { Toast } from 'mint-ui';
import { Indicator } from 'mint-ui';
import $ from '../../../static/js/jquery.min.js';
import axios from 'axios';
import qs from 'qs';
export default {
name: "findProject",
    data () {
        return {
            showPromptWrap: false, // 
            linkmanName: "", // 
            linkmanTel: "", // 
            organization: "", // 
            jobTitle: "", // 
            needs: "" // 
        }
    },
    computed:{

    },
    methods:{
        // 
        navBack(){ 
            this.showPromptWrap = true;   
        },
        goLeave(){
            this.$router.push({path: '/memberNeeds'}); 
        },
        // 
        submitBtn(){
            let reg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
            if(this.linkmanName == "" || this.linkmanTel == "" || this.organization == "" || this.jobTitle == "" || this.needs == ""){
                Toast({
                    message: '',
                    duration: 3000
                });
            }else if(!reg.test(this.linkmanTel)){
                Toast({
                    message: '',
                    duration: 3000
                });
            }else{
                Indicator.open({
                    text: '...',
                    spinnerType: 'fading-circle'
                });
                let url = this.changeData() + '/member/auth/saveDemandInfo'
                axios(url,{
                    method: 'post',
                    params: {
                        type: 0,    //string      0  1  2 
                        contactName: this.linkmanName,  //string     
                        contactPhone: this.linkmanTel,  //string     
                        email: "",  //string     
                        company: this.organization,  //string     
                        position: this.jobTitle,   //string     
                        description: this.needs //string     
                    }
                }).then(data => {
                    // console.log(data);
                    if(data.data.code == 101){
                        Indicator.close();
                        Toast({
                            message: '',
                            duration: 3000
                        });
                        this.$router.push({path: '/home'}); 
                    }else{
                        Toast({
                            message: data.data.message,
                            duration: 3000
                        });
                    }
                }).catch(err => {
                    console.log(err)
                });

            }
        },
        // 
        userData(){
            let url = this.changeData() + '/member/auth/getMemberInfo'
            axios(url,{
                method: 'post',
            }).then(data => {
                // console.log(data);
                // this.userDataObj = data.data.memberInfo;
                this.linkmanName = data.data.memberInfo.name;
                this.linkmanTel = data.data.memberInfo.phone;
                this.organization = data.data.memberInfo.company;
                this.jobTitle = data.data.memberInfo.position;
                console.log(this.userDataObj);
            }).catch(err => {
                console.log(err)
            });
        }
    },
    mounted() {
        this.userData();
    	
    }
}
</script>

<style lang="scss" type="text/scss" scoped>
@import "../../../static/css/commonNav.css";
.findProject{
	width: 7.5rem;
}	
.findProjectUl{
    width: 6.9rem;
    height: 7rem;
    padding: 0 .3rem 0 .3rem;
    background: #fff;
    li{
        width: 6.9rem;
        height: .9rem;
        border-bottom: 1px solid #eaeaea;
        line-height: .9rem;
        box-sizing: border-box;
        p{
            width: 2.1rem;
            float: left;
            color: #333;
            font-size: .3rem;
        }
        input{
            width: 4.8rem;
            line-height: .4rem;
            background: transparent;
            font-size: .28rem;
            color: #333;
        }
        textarea{
            width: 6.5rem;
            height: 1.54rem;
            background: #fff;
            border: 1px solid #eaeaea;
            resize:none;
            -webkit-appearance: none;
            outline: none;
            font-size: .3rem;
            padding: .2rem;
            -webkit-tap-highlight-color: rgba(0,0,0,0);
        }
    }
    .lastLi{
        border-bottom: none;
    }
}
</style>



