<template>
<div class="newsInfo" ref="box">
    <div class="navTop">
        <img src="../../../static/images/common/backWhite.png" class="backWhite" @click="navBack()">
        <img src="../../../static/images/common/collect.png" class="collectIcon" @click="collectBtn()" v-if="collectId == 0">
        <img src="../../../static/images/common/collect1.png" class="collectIcon" @click="cancelCollectBtn()" v-else>
    </div>
    <div class="infoWrap">
        <h3 class="infoTitle">{{newsObj.title}}</h3>
        <h6 class="infoDate">{{newsObj.createTime}}</h6>
        <div class="infoContent" v-html="newsObj.content"></div>
    </div>
</div>
</template>
<script>
import { Toast } from 'mint-ui';
import $ from '../../../static/js/jquery.min.js';
import axios from 'axios';
import qs from 'qs';
export default {
    name: 'newsInfo',
    data () {
        return {
            urlId: "",
            move: "",
            newsObj: {},
            collectId: 0, // 0  1 
        }
    },
    beforeRouteEnter (to, from, next) {
        var u = navigator.userAgent;
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
        // XXX: iOSHTML5 History
        if (isiOS && to.path !== location.pathname) {
            // location.replace
            location.assign(to.fullPath)
        } else {
            next()
        }
    },
    methods:{
        // 
        navBack(){
            if(this.$route.query.url){
                this.$router.go(-1);
            }else{
                this.$router.push({path: '/home'});
            }
        },
        
        // 
        newsData(){
            let url = this.changeData() + '/information/detail'
            axios(url,{
                method: 'post',
                params: {
                    id: this.urlId
                }
            }).then(data => {
                // console.log(data);
                this.newsObj = data.data.informationDetail;
                this.collectId = data.data.informationDetail.is_collect;
            }).catch(err => {
                console.log(err)
            });
        },

        // 
        collectBtn(){
            if(!localStorage.token){
                this.$router.push({path: '/login'});
            }else{
                let url = this.changeData() + '/member/auth/collect'
                axios(url,{
                    method: 'post',
                    params: {
                        type: "0", // 0 1
                        id: this.urlId //id
                    }
                }).then(data => {
                    // console.log(data);
                    this.collectId = 1;
                    if(data.data.code == 101){
                        Toast({
                            message: '',
                            duration: 3000
                        });
                    }else{
                        Toast({
                            message: data.data.message,
                            duration: 3000
                        });
                    }
                }).catch(err => {
                    console.log(err)
                });
            }
        },
        // 
        cancelCollectBtn(){
            if(!localStorage.token){
                this.$router.push({path: '/login'});
            }else{
                let url = this.changeData() + '/member/auth/cancelCollect'
                axios(url,{
                    method: 'post',
                    params: {
                        collectType: "0", // 0 1
                        id: this.urlId //id
                    }
                }).then(data => {
                    // console.log(data);
                    if(data.data.code == 101){
                        this.collectId = 0;
                        Toast({
                            message: '',
                            duration: 3000
                        });
                    }else{
                        Toast({
                            message: data.data.message,
                            duration: 3000
                        });
                    }
                }).catch(err => {
                    console.log(err)
                });
            }
        },

        fenxiangFun(){//
            // console.log(window.location.href.split('#')[0])
            var that = this;
            this.$http({
                url:this.changeData() + '/member/weChatShare',
                method: "post",
                params: {
                    url: window.location.href.split('#')[0]
                }
            }).then(function(res){
                // console.log(res)
                wx.config({
                    //debug: true, // ,apialertpclogpc
                    appId: res.data.appId, // 
                    timestamp: res.data.timestamp, // 
                    nonceStr: res.data.nonceStr, // 
                    signature: res.data.signature,// 
                    jsApiList: ["onMenuShareAppMessage","onMenuShareTimeline"] // JS
                });

                //
                wx.ready(function(){
                    // configreadyconfigconfigreadyready
                    //
                    wx.onMenuShareAppMessage({
                        title: that.newsObj.title, // 
                        desc: '', // 
                        link: window.location.href.split('#')[0], // JS
                        imgUrl: "https://up-static.oss-cn-beijing.aliyuncs.com/Config/11544678901_.pic_hd.jpg", // 
                        type: '', // ,musicvideolinklink
                        dataUrl: '', // typemusicvideo
                        success: function () {
                            // 
                        }
                    });

                    //
                    wx.onMenuShareTimeline({
                        title: that.newsObj.title, // 
                        link: window.location.href.split('#')[0], // JS
                        imgUrl: "https://up-static.oss-cn-beijing.aliyuncs.com/Config/11544678901_.pic_hd.jpg", // 
                        success: function () {
                            // 
                        }
                    });
                });
                wx.error(function(res){
                    // configerrorconfigdebugresSPA
                });
            }.bind(this)).catch(function(err){
              console.log("",err)
            });
        },

    },
    mounted() {
        // alert(window.location.href.split('#')[0]);
        //Url
        function GetRequest() {
            var url = location.search; //url"?"
            var theRequest = new Object();
            if (url.indexOf("?") != -1) {
                var str = url.substr(1);
                var strs = str.split("&");
                for (var i = 0; i < strs.length; i++) {
                    theRequest[strs[i].split("=")[0]] = decodeURI(strs[i].split("=")[1]);
                }
            }
            return theRequest;
        }
        var request = new Object();
        request = GetRequest();

        this.urlId = request['id'];
        this.move = request['move'];
        if(this.move == "ios"){
            $(".navTop").hide();
        }else if(this.move == "android"){
            $(".navTop").hide();
            $(".infoWrap").css({"padding":".3rem .2rem .3rem .2rem"});
        }
        this.newsData()
    },
    created() {

    },
    updated(){
        // 
        this.fenxiangFun();
    }
}
</script>

<style scoped lang="scss">
@import "../../../static/css/commonNav.css";
.newsInfo{
    width: 7.5rem;
    height: 100vh;
    background: #fff;
}
.infoWrap{
    background: #fff;
    padding: 1.4rem .2rem .3rem .2rem;
    .infoTitle{
        color: #333;
        font-size: .36rem;
        text-align: justify;
        line-height: .5rem;
        /*text-align: center;*/
    }
    .infoDate{
        font-size: .28rem;
        color: #898989;
        margin-top: .2rem;
        margin-bottom: .3rem;
    }
    .infoContent{
        width: 7.1rem;
        p{
            color: #666;
            font-size: .28rem;
            text-indent: 2em;
            line-height: .4rem;
        }
    }
}
</style>
<style scoped>
.infoContent >>> img{
    max-width: 7.1rem;
    max-height: 10rem;
}
</style>




