<template>
<div class="buyHouse">
    <div class="navTop">
        <img src="../../../static/images/common/backWhite.png" class="backWhite" @click="navBack()">
        <h2 class="navTopH2"></h2>
    </div>
    <div class="buyHouseWrap">
        <ul>
            <li class="buyHouseLi1">
                <p>*</p>
                <input type="" name="" placeholder="" v-model="projectName">
            </li>
            <li>
                <p>*</p>
                <input type="" name="" placeholder="" v-model="floorNumber">
            </li>
            <li>
                <p>*</p>
                <input type="" name="" placeholder="" v-model="roomNumber">
            </li>
            <li>
                <p>*</p>
                <input type="" name="" placeholder="" v-model="buyerName">
            </li>
            <li>
                <p>*</p>
                <input type="tel" name=""  placeholder="" onkeyup="this.value=this.value.replace(/\D/g,'')" maxlength="11" v-model="buyerTel">
            </li>
            <li>
                <p></p>
                <input type="" name="" placeholder="" v-model="sellName">
            </li>
            <li>
                <p></p>
                <input type="" name="" placeholder="" onkeyup="this.value=this.value.replace(/\D/g,'')" maxlength="11" v-model="sellTel">
            </li>
        </ul>
    </div>
    <div class="submitBtn" @click="submitBtn()"></div>
    <!--  -->
    <div class="promptWrap" v-if="showPromptWrap">
        <div class="promptBox">
            <h5></h5>
            <ul>
                <li class="li1" @click="goLeave()"></li>
                <li class="li2" @click="showPromptWrap = false"></li>
            </ul>
        </div>
    </div> 
</div>
</template>

<script>
import { Toast } from 'mint-ui';
import $ from '../../../static/js/jquery.min.js';
import axios from 'axios';
import qs from 'qs';
export default {
name: "buyHouse",
    data () {
        return {
        	projectName: "", // 
            floorNumber: "", // 
            roomNumber: "", // 
            buyerName: "", // 
            buyerTel: "", // 
            sellName: "", // 
            sellTel: "", // 
            showPromptWrap: false, // 
        }
    },
    computed:{

    },
    methods:{
        // 
        navBack(){  
            this.showPromptWrap = true;      
        },
        goLeave(){
            this.$router.push({path: '/home'});   
        },
        // 
        submitBtn(){
            let reg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
            if(this.sellTel != ""){
                if(!reg.test(this.sellTel)){
                    Toast({
                        message: '',
                        duration: 3000
                    });
                }else{
                    this.formData()
                }
            }else{
                this.formData();
            }
        },
        // 
        formData(){
            let reg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
            if(this.projectName == "" || this.floorNumber == "" || this.roomNumber == "" || this.buyerName == "" || this.buyerTel == ""){
                Toast({
                    message: '',
                    duration: 3000
                });
            }else if(!reg.test(this.buyerTel)){
                Toast({
                    message: '',
                    duration: 3000
                });
            }else{
                let url = this.changeData() + '/member/auth/buyHouse'
                axios(url,{
                    method: 'post',
                    params: {
                        projectName: this.projectName, //string     
                        bulidingNo: this.floorNumber,  //string     
                        unitNo: this.roomNumber,  //string     
                        buyerName: this.buyerName,  //string     
                        buyerPhone: this.buyerTel,  //string     
                        saleName: this.sellName,    //string     
                        salePhone: this.sellTel   //string     
                    }
                }).then(data => {
                    // console.log(data);
                    if(data.data.code == 101){
                        Toast({
                            message: '',
                            duration: 3000
                        });
                        this.$router.push({path: '/home'}); 
                    }else{
                        Toast({
                            message: data.data.message,
                            duration: 3000
                        });
                    }
                }).catch(err => {
                    console.log(err)
                });
            }
        },
        // 
        userData(){
            let url = this.changeData() + '/member/auth/getMemberInfo'
            axios(url,{
                method: 'post',
            }).then(data => {
                // console.log(data);
                // this.userDataObj = data.data.memberInfo;
                this.buyerName = data.data.memberInfo.name;
                this.buyerTel = data.data.memberInfo.phone;
                console.log(this.userDataObj);
            }).catch(err => {
                console.log(err)
            });
        }
        
    },
    mounted() {
        this.userData();
        
    },
    created() {
        
    }
}
</script>

<style lang="scss" type="text/scss" scoped>
@import "../../../static/css/commonNav.css";
.buyHouse{
	width: 7.5rem;
}
.buyHouseWrap{
    width: 6.9rem;
    height: 6.3rem;
    padding: 1rem .3rem 0 .3rem ;
    background: #fff;
    ul{
        li{
            width: 6.9rem;
            height: .9rem;
            border-top: 1px solid #eaeaea;
            line-height: .9rem;
            box-sizing: border-box;
            p{
                width: 2.9rem;
                float: left;
                color: #333;
                font-size: .3rem;
            }
            input{
                width: 4rem;
                line-height: .4rem;
                background: transparent;
                font-size: .28rem;
                color: #333;
            }
        }
        .buyHouseLi1{
            border-top: none;
        }
    }
}
</style>



